import http from '@/api/http.js'
const app = getApp();
export default{
	data(){
		return{
			imag: app.globalData.imag,
			systeminfo:{}
		}
	},
	onLoad() {
		this.getSystemInfo()
	},
	onShow() {
		this.getSystemInfo()
	},
	methods:{
		toSystemMsg(){
			uni.navigateTo({
				url:'/pages/index/msg/systemMsg'
			})
		},
		getSystemInfo(){
			let data = {
					token:uni.getStorageSync('token')
				}
			http('/api/Message/getMessageFind','post',data).then(res=>{
				console.log(res)
				this.systeminfo = res.data.data
			})
		}
	}
}
